/* 맞춤매물관리-안내 */
.img_request_guide{width:739px;height:260px;background:url(#{$imgUrl-center}/sp_guide.png) no-repeat 0 0;text-indent:-9999px}
.content .h_request_guide1,.content .h_request_guide2,.content .h_request_guide3{height:14px;margin:27px 0 13px;padding:0;background:url(#{$imgUrl-center}/sp_guide.png) no-repeat;text-indent:-9999px}
.content .h_request_guide1{width:80px;margin-top:32px;background-position:0 -264px}
.content .h_request_guide2{width:80px;background-position:-88px -264px}
.content .h_request_guide3{width:101px;background-position:-176px -264px}
.p_request_guide{line-height:19px}
.lst_request_guide{margin:39px 0 24px}
.lst_request_guide li{margin-top:26px}
.lst_request_guide li:first-child{margin-top:0}
/* 블릿타입_Text*/
.bullet_lst li{position:relative;margin-top:5px;line-height:15px;font-size:11px;letter-spacing:-1px;vertical-align:top}
.bullet_lst .text_type{padding-left:8px}
.bullet_lst .text_type .point{position:absolute;top:0;left:0;color:#444}
.bullet_lst.color_accent1 li,.color_accent1 .text_type .point{color:#3e75ac}
.bullet_lst.color_accent2 li,.color_accent2 .text_type .point{color:#f5433d}
.account .bullet_lst:first-child{margin:-4px 0 5px}
.account .bullet_lst li{padding-top:0;padding-bottom:0}
.bullet_lst li .btn_mail,.bullet_lst li .btn_bordered{margin-top:-1px;#{$ie7-hack}margin-top:0}
/* border_btn */
.btn_bordered{display:inline-block;padding:3px 4px 0 3px;border:1px solid #c3c3c3;font-size:11px;line-height:11px;color:#8b8b8b;text-align:center}
.btn_bordered:hover,.btn_bordered:focus,.btn_bordered:active{text-decoration:none}
.table_type2 td a.btn_bordered{margin-right:1px;color:#8b8b8b;text-decoration:none}

.lst_type1{margin-bottom:5px;line-height:16px;}
.lst_type1 .sec_tit{margin:5px 7px 0 0}
.lst_type1 .sec_tit input[type="checkbox"]{position:relative;top:1px;margin-right:5px;}

.homeFace{float:left;width:695px;margin-right:30px}
.twin_area{overflow: hidden}
.ad_banner{margin-bottom:10px;overflow: hidden;position: relative}
.ad_banner a,.snb_ad a{display:inline-block}
.ad_banner .ad_paging{position: absolute;bottom:10px;right:10px;display: inline-block}
.ad_banner .ad_paging span{display: inline-block;float:left;margin-left:5px;width:10px;height:10px;background:url('#{$imgUrl-center}/banner/ad_landad_btn.png') no-repeat 0 -12px}
.ad_banner .ad_paging span:first-child{margin-left:0}
.ad_banner .ad_paging span.on{background:url('#{$imgUrl-center}/banner/ad_landad_btn.png') no-repeat 0 0}
.ad_banner .ad_paging span a{display: inline-block;width:10px;height:10px}
.snb_ad{border-top:1px solid #dcdde0;margin-top:44px;padding-top:20px}

.content_talk{margin-top:55px;min-height:645px}
.content_talk h2{margin-bottom:15px}
.content_talk h2 .h_set_talk{display:inline-block;width:70px;height:17px;background:url('#{$imgUrl-center}/chat/h_set_talk.png') no-repeat}
.set_talk_tbl{width:100%;border:1px solid #dcdedf;border-collapse: collapse}
.set_talk_tbl th,.set_talk_tbl td{font-size:14px;color:#444;font-family: "나눔고딕", NanumGothic, "돋움", Dotum, "굴림", Gulim, AppleGothic, Sans-serif;border-left:1px solid #f0f0f0}
.set_talk_tbl th{background:#f9fafb;height:48px;border-bottom:1px solid #e5e7e8;text-align: center}
.set_talk_tbl th:first-child,.set_talk_tbl td:first-child{border-left:0}
.set_talk_tbl td{position:relative;height:60px;font-weight:normal;text-align: center}
.set_talk_tbl .stateBtn,.set_talk_tbl .tglBtn{display:inline-block;height:33px;background:url('#{$imgUrl-center}/chat/sp_talk_btn.png') no-repeat}
.set_talk_tbl .stateBtn button,.set_talk_tbl .tglBtn button{display:inline-block;overflow: visible;height:33px;width:100%;cursor: pointer}
.set_talk_tbl .stateBtn.btns_on{width:85px;background-position:-95px -129px}
.set_talk_tbl .stateBtn.btns_off{width:85px;background-position:-95px -172px}
.set_talk_tbl .stateBtn.btns1{width:85px;background-position:0 0}
.set_talk_tbl .stateBtn.btns2{width:85px;background-position:0 -43px}
.set_talk_tbl .stateBtn.btns3{width:85px;background-position:0 -86px}
.set_talk_tbl .stateBtn.btns4{width:85px;background-position:0 -129px}
.set_talk_tbl .stateBtn.btns5{width:85px;background-position:0 -172px}
.set_talk_tbl .stateBtn.btns6{width:85px;background-position:0 -125px}
.set_talk_tbl td.master{padding-left:0;text-align:center}
.set_talk_tbl .stateBtn.del{position:static;width:85px;background-position:-95px -86px;margin:0 6px}
.set_talk_tbl .tglBtn{position:static;width:85px;background:url('#{$imgUrl-center}/chat/sp_talk_btn.png') -95px 0;text-align: left}
.set_talk_tbl .tglBtn.btns_on{background-position:-95px 0}
.set_talk_tbl .tglBtn.btns_off{background-position:-95px -43px}
.go_partner{color:#828282;font-size:13px;margin-top:21px}
.go_partner a{display:inline-block;width:124px;height:14px;background:url('#{$imgUrl-center}/chat/go_partner.png') no-repeat;vertical-align:middle}
.talk_guide{margin:24px 0 48px}
.talk_guide li{font-size: 13px; color:#828282;margin-top:10px}
.talk_guide li:first-child{margin-top:0}
.talk_guide li .middot{display: inline-block;width:2px;height:2px;background:#cfcfcf;margin-right:10px;position: relative;top:-3px}
.go_link{overflow: hidden;background:#f9fafb;padding:32px 0 31px 40px;height:148px;box-sizing: border-box}

.go_link .goBox{float:left;width:400px;}
.go_link .goBox *{font-family: "나눔고딕", NanumGothic, "돋움", Dotum, "굴림", Gulim, AppleGothic, Sans-serif}
.go_link .goBox h3{background:url('#{$imgUrl-center}/chat/h2_golink1.png') no-repeat;width:78px;height:13px}
.go_link .goBox + div{margin-left:81px}
.go_link .goBox + div h3{background:url('#{$imgUrl-center}/chat/h2_golink2.png') no-repeat;width:110px;height:13px}
.go_link .goBox p{font-size:13px;color:#828282;margin-top:11px}
.go_link .goBox a{display:inline-block;width:150px;height:30px;background: #7993ac;text-align: center;color:#fff;line-height: 30px;margin-top:19px}
.go_link .goBox a:hover{text-decoration: none}

.prms_tx{border:1px solid #e5e5e5;margin-top:40px;padding:17px 20px 16px;line-height: 18px}